function Store(){
    // Load user from localstorage and set value of isAuthenticated

    if(JSON.parse(localStorage.getItem("piggy-app-user"))) {
        
        this.user=JSON.parse(localStorage.getItem("piggy-app-user"));
        this.isAuthenticated=true;
        // debugger;
    }

}

Store.prototype.saveUser=function(user) {
    this.user=user;
    localStorage.setItem("piggy-app-user", JSON.stringify(this.user));
    console.log(this)
    this.isAuthenticated=true;
}

Store.prototype.getUser=function(){
    return this.user;
}
export default new Store();