import React from 'react';
import { Row, Col, Button, Panel, Modal, FormGroup, FormControl, ControlLabel } from 'react-bootstrap';
import './currentAccount.css';
import Store from './../user/userStore';
import axios from 'axios';
import MoneyFormater from './../moneyFormater';

class UpdateMainAccountModal extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            "showModal": false,
            "newBankvalue": ''
        }
    }
    handleChange = (e) => {
        this.setState({ newBankvalue: e.target.value });
    }
    handleClose = () => {

        this.setState({ showModal: false, newBankvalue: '' });

    }

    updateMainBal = () => {
        // let newState = Object.assign({}, this.state);
        // newState.funds.push({ name: this.state.newBankvalue, amount: 0 });
        // newState.newBankvalue = '';
        // this.setState(newState);
        // debugger;
    

        this.handleClose();
        this.props.onUpdate(this.state.newBankvalue);
        // debugger;
       
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.showModal !== this.state.showModal) {
            this.setState({ showModal: nextProps.showModal });
        }
    }

    render() {

        return (
            <div className="static-modal">
                <Modal show={this.state.showModal} onHide={this.handleClose}>
                    <Modal.Header>
                        <Modal.Title>Enter Name</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <form>
                            <FormGroup
                                controlId="formBasicText"
                            >
                                <ControlLabel>Working example with validation</ControlLabel>
                                <FormControl
                                    onChange={this.handleChange}
                                    type="text"
                                    value={this.state.newBankvalue}
                                    placeholder="Enter text"
                                />
                                <FormControl.Feedback />
                            </FormGroup>
                        </form>
                    </Modal.Body>

                    <Modal.Footer>
                        <Button onClick={this.handleClose}>Close</Button>
                        <Button onClick={this.updateMainBal} bsStyle="primary">Save changes</Button>
                    </Modal.Footer>
                </Modal>
            </div>

        );
    }
}
class MainAccount extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            "showModal": false
        };


    }
    openUpdateModal = () => {
        this.setState({ showModal: true });
    }

    updateMainBal = (mainBalanceAmount) => {
        // debugger;
        // this.setState({"mainBalanceAmount":mainBalanceAmount});
        var self=this;
        let data = {
            method: 'POST',
            url: 'https://bank-piggy.herokuapp.com/v1/account',
            headers:
            {
                authorization: 'Bearer ' + Store.getUser().token,
                'content-type': 'application/json'
            },
            data: { balance: mainBalanceAmount },
            json: true
        };

        axios(data)
        .then(function(response){
            console.log(response);
            self.setState({ showModal: false });
            self.props.refresh();
        })
        .catch(function(error){
            alert(error);
        });
    }
    render() {
        return (

            <Row className="element-root">
                <Panel>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <h5>Main Account Balance : {MoneyFormater.format(this.props.mainBalance)}</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <Button onClick={() => this.openUpdateModal()}><b>Update</b></Button>
                        </Col>
                    </Row>
                </Panel>
                <UpdateMainAccountModal showModal={this.state.showModal}
                    onUpdate={(amount) => { this.updateMainBal(amount) }} />
            </Row>
        );
    }
}

//   export default RequiredAuth(MainAccount);
export default MainAccount;