import React, { Component } from 'react';
import { Grid } from 'react-bootstrap';
import './App.css';

import { Row, Col,Button,Panel } from 'react-bootstrap';
import CurrentAccount from './CurrentAccount';
import Accounts from './accounts';
import MainAccount from './mainAccount';
import Store from './../user/userStore';
import axios from "axios";

class HomePage extends Component {

    constructor(props) {
        super(props);
        this.state={
            remainingBalance:4400,
            funds :[],
            mainBalance:90500,
            isLoading:true
            
        }

        this.getDataFromServer=this.getDataFromServer.bind(this);
    }

    getDataFromServer() {
        var self=this;
        var options = {
            method: 'GET',
            url: 'https://bank-piggy.herokuapp.com/v1/account',
            headers: { authorization: 'Bearer '+ Store.getUser().token}
        };

        axios(options)
        .then(function(response){
            console.log(response.data.account.piggybanks)
            response.data.account.piggybanks.forEach(bank => {
                if(!bank.amount)
                    bank.amount=0;
            });

            let newState=Object.assign({},self.state);
            newState.funds=response.data.account.piggybanks;
            newState.remainingBalance= response.data.account.remainingBalance;
            newState.mainBalance=response.data.account.mainBalance;
            newState.isLoading=false;
            self.setState(newState);
        })
        .catch(function(error){
            alert(error);
        })
    }
    componentDidMount() {
        this.getDataFromServer();
    }
    goToTransferPage = ()=>{
        this.props.history.push("/save");
    }
    render() {

        if(this.state.isLoading)
            return (
                
                <p style={{
                
                'paddingTop' :"250px",
                'textAlign':"center"
                }}>Loading...</p>
            );
        return (
            <Grid>
                <CurrentAccount remainingBalance={this.state.remainingBalance} 
                goToTransferPage={this.goToTransferPage}
                />

                <Accounts funds={this.state.funds} refresh={this.getDataFromServer}/>

                <MainAccount mainBalance={this.state.mainBalance} refresh={this.getDataFromServer}/>

            </Grid>
        );
    }
}

export default HomePage;
