import React from 'react';
import {
    Row, Col, Button, Panel,
    FormGroup, FormControl, ControlLabel
} from 'react-bootstrap';
import './../home/currentAccount.css';
import Store from './userStore';
import axios from "axios";

class Login extends React.Component {

    attemptLogin = () => {
        var self=this;
        // Send a POST request
        axios({
            method: 'post',
            url: 'https://bank-piggy.herokuapp.com/v1/user/login',
            data: {
                // email: 'tauseef.naqvi786@gmail.com',
                // password: 'india123'
                email: this.email,
                password: this.password
            }
        })
            .then(function (response) {
                // console.log(response)
                Store.saveUser({
                    email: "jafar912@gmail.com",
                    token : response.data.token
                });
                self.props.history.push("/");
            })
            .catch(function (error) {
                console.log(error);
                alert(JSON.stringify(error.response.data.msg));
            })


    }

    handleEmailChange = (e)=> {
        this.email=e.target.value;
    }
    handlePasswordChange =(e)=>{
        this.password=e.target.value;
    }
    render() {
        return (

            <Row className="element-root">
                <Panel>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <FormGroup
                            >
                                <FormControl
                                    onChange={this.handleEmailChange}
                                    type="email"
                                    placeholder="Enter email"
                                />
                                <br />
                                <FormControl
                                    onChange={this.handlePasswordChange}
                                    type="password"
                                    placeholder="Enter password"
                                />
                                <FormControl.Feedback />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <Button onClick={this.attemptLogin} bsStyle="primary"><b>Login</b></Button>
                        </Col>
                    </Row>
                </Panel>
            </Row>
        );
    }
}

export default Login;