import React from 'react';
import {
    Modal, Button, Row,
    Col, ListGroup, Panel,
    FormGroup, FormControl, ControlLabel,
    ListGroupItem
} from 'react-bootstrap';
import './accounts.css';
import axios from 'axios';
import Store from './../user/userStore';
import MoneyFormater from './../moneyFormater';

class ListItems extends React.Component {

    // @Todo : Proper unique key for every added element
    createList = () => {
        let list = []
        let funds = this.props.funds;

        // debugger;
        // Outer loop to create parent
        for (let i = 0; i < funds.length; i++) {
            let fundComplete = [];
            fundComplete.push(<Col key={new Date() + "name" + i} className="fund-name" xs={6} md={6} lg={6}>
                {funds[i].name} : </Col>);
            fundComplete.push(<Col key={new Date() + "amt" + i} className="fund-amount" xs={6} md={6} lg={6}>
                {MoneyFormater.format(funds[i].amount)} </Col>);
            //Create the parent and add the children
            list.push(<ListGroupItem key={new Date() + "items" + i}><Row>{fundComplete}</Row></ListGroupItem>)
        }
        return list
    }
    render() {

        return (

            <ListGroup>

                {this.createList()}
            </ListGroup>
        );
    }
};

class Accounts extends React.Component {

    constructor(props, context) {
        super(props, context);

        this.addNewPiggyBank = this.addNewPiggyBank.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.saveNewPiggyBank = this.saveNewPiggyBank.bind(this);

        this.state = {
            showModal: false,
            newBankvalue: ''
        };
    }

    saveNewPiggyBank() {
        let self=this;
        var options = { 
            method: 'POST',
            url: 'https://bank-piggy.herokuapp.com/v1/account/piggy-bank',
            headers:
            {
                authorization: 'Bearer '+ Store.getUser().token,
                'content-type': 'application/json'
            },
            data: { name: this.state.newBankvalue },
            json: true
        };
        axios(options)
        .then(function(response){

            self.props.refresh();
            // debugger;
        //     let newState = Object.assign({}, self.state);
        // newState.funds.push({ name: self.state.newBankvalue, amount: 0 });
        // newState.newBankvalue = '';
        // this.setState(newState);
        self.handleClose();
        })
        .catch(function(error){
            alert(error);
        });
        
    }

    handleChange(e) {
        this.setState({ newBankvalue: e.target.value });
    }
    handleClose() {

        this.setState({ showModal: false, newBankvalue: '' });
    }


    addNewPiggyBank() {
        this.setState({ showModal: true });
    }

    render() {
        return (

            <Row className="piggy-bank-root">
                <Panel>
                    <Panel.Heading>
                        <Panel.Title componentClass="h3">Piggy Banks </Panel.Title>
                    </Panel.Heading>
                    <Panel.Body>
                        <Row className="add-piggy-bank-row">
                            <Col xs={12} md={12} sm={12}>
                                <Button onClick={() => this.addNewPiggyBank()} className="add-piggy-bank-button" >
                                    <b>Add</b>
                                </Button>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={12} sm={12}>
                                <ListItems funds={this.props.funds} />
                            </Col>
                        </Row>

                    </Panel.Body>
                </Panel>
                <div className="static-modal">
                    <Modal show={this.state.showModal} onHide={this.handleClose}>
                        <Modal.Header>
                            <Modal.Title>Enter Name</Modal.Title>
                        </Modal.Header>

                        <Modal.Body>
                            <form>
                                <FormGroup
                                    controlId="formBasicText"
                                >
                                    <ControlLabel>Working example with validation</ControlLabel>
                                    <FormControl
                                        onChange={this.handleChange}
                                        type="text"
                                        value={this.state.newBankvalue}
                                        placeholder="Enter text"
                                    />
                                    <FormControl.Feedback />
                                </FormGroup>
                            </form>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button onClick={this.handleClose}>Close</Button>
                            <Button onClick={this.saveNewPiggyBank} bsStyle="primary">Save changes</Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </Row>

        );
    }
}


export default Accounts;