import React from 'react';
import {
    Row, Col, Button,
    MenuItem, DropdownButton,
    Panel, FormGroup, FormControl, ControlLabel
} from 'react-bootstrap';
import './../home/currentAccount.css';
import MoneyFormater from './../moneyFormater';

import axios from 'axios';
import Store from './../user/userStore';

class DropDownWithItems extends React.Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
            "dropDownTitle": "Select Piggy Bank"
        }
    }

    onFundSelected = (index) => {
        // console.log(event);

        if (this.state.dropDownTitle !== index) {
            let newState = Object.assign({}, this.state);
            newState.dropDownTitle = this.props.funds[index].name;
            this.setState(newState);
            this.props.onFundSelected(index);
        }

    }


    // @Todo : Proper unique key for every added element
    createDropDown = () => {
        let list = []
        let funds = this.props.funds;
        // Outer loop to create parent
        for (let i = 0; i < funds.length; i++) {
            // let fundComplete = [];
            // fundComplete.push(<Col key={new Date()+"name"+i} className="fund-name" xs={6} md={6} lg={6}>
            //     {funds[i].name} : </Col>);
            //Create the parent and add the children
            list.push(<MenuItem key={i + new Date()} onSelect={this.onFundSelected} eventKey={i} >{funds[i].name}</MenuItem>)
        }
        return list
    }
    render() {

        return (
            <DropdownButton
                bsStyle="default"
                title={this.state.dropDownTitle}
                key="dropDownButtonInSaveToBank"
                id={`dropdown-basic`}>

                {this.createDropDown()}
            </DropdownButton>
        )
    }
};

class SaveToBank extends React.Component {

    currentFund = 0;
    forceUpdateAmounts = false;
    onFundSelected = (fundIndex) => {
        this.currentFund = fundIndex;

        let newState = Object.assign({}, this.state);
        newState.fundSelected = true;


        // debugger;
        if (!isNaN(Number(this.state.transferAmountValue))) {

            let forceObject = {
                target: {
                    value: newState.transferAmountValue
                }
            }
            this.forceUpdateAmounts = true;
            this.handleTransferAmountChange(forceObject);
        }


        this.setState(newState);
    }

    handleTransferAmountChange = (e) => {
        let newState = Object.assign({}, this.state);
        newState.transferAmountValue = e.target.value;
        if ((this.state.fundSelected || this.forceUpdateAmounts) && !isNaN(Number(this.state.transferAmountValue))) {
            console.log("value changed");

            newState.funds[this.currentFund].newAmount = newState.funds[this.currentFund].amount + Number(newState.transferAmountValue);
            newState.newRemainingBalance = newState.remainingAccountBalance - Number(newState.transferAmountValue);

            console.log(newState.funds[this.currentFund].newAmount);
        }
        this.setState(newState);
    }

    constructor(props, context) {
        super(props, context);

        this.state = {
            remainingAccountBalance: 14400,
            transferAmountValue: '',
            fundSelected: false,
            isLoading:true,
            funds: [{
                name :'',
                amount : 0
            }]
        }
    }

    getDataFromServer() {
        var self=this;
        var options = {
            method: 'GET',
            url: 'https://bank-piggy.herokuapp.com/v1/account',
            headers: { authorization: 'Bearer '+ Store.getUser().token}
        };

        axios(options)
        .then(function(response){
            console.log(response.data.account.piggybanks)
            response.data.account.piggybanks.forEach(bank => {
                if(!bank.amount)
                    bank.amount=0;
            });

            let newState=Object.assign({},self.state);
            newState.funds=response.data.account.piggybanks;
            newState.remainingAccountBalance=response.data.account.remainingBalance;
            newState.currentBalance = response.data.account.remainingBalance;
            newState.isLoading=false;
            self.setState(newState);
        })
        .catch(function(error){
            alert(error);
        })
    }
    componentDidMount() {
        this.getDataFromServer();
    }

    goToHomePage = () => {
        this.props.history.push("/");
    }

    executeTransfer=()=> {
        let self=this;
        var options = {
            method: 'PATCH',
            url: 'https://bank-piggy.herokuapp.com/v1/account/piggy-bank',
            headers:
            {
                authorization: 'Bearer '+Store.getUser().token,
                'content-type': 'application/json'
            },
            data: { amount: this.state.funds[this.currentFund].newAmount, 
                id: this.state.funds[this.currentFund]._id },
            json: true
        };
        axios(options)
        .then(function(response){
            // Redirect to home page
            console.log(response.data);
            self.goToHomePage();
        })
        .catch(function(error){
            alert(error);
        });
    }
    render() {

        if(this.state.isLoading)
            return (
                
                <p style={{
                
                'paddingTop' :"250px",
                'textAlign':"center"
                }}>Loading...</p>
            );
        return (

            <Row className="element-root">
                <Panel>
                    <Row style={{ "textAlign": "left" }}>
                        <Col xs={3}>
                            <Button onClick={this.goToHomePage}>{"<"}</Button>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <h5>Current Remaining Balance : {MoneyFormater.format( this.state.currentBalance ? this.state.currentBalance : this.state.remainingAccountBalance)}</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <DropDownWithItems funds={this.state.funds} onFundSelected={this.onFundSelected} />
                        </Col>
                    </Row>
                    <Row><br /><br /></Row>
                    <Row>
                        <Col xs={12} md={12} sm={12}>
                            <form>
                                <FormGroup
                                    controlId="formBasicText"
                                >
                                    <ControlLabel>Enter Amount to transfer</ControlLabel>
                                    <FormControl
                                        onChange={this.handleTransferAmountChange}
                                        type="text"
                                        placeholder="Enter Amount"
                                    />
                                    <FormControl.Feedback />
                                </FormGroup>
                            </form>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={6} md={6} sm={6}>
                            <h5 hidden={this.state.fundSelected}>No fund selected</h5>
                            <h5 hidden={!this.state.fundSelected}> {this.state.funds[this.currentFund].name} balance : 
                                {MoneyFormater.format(this.state.funds[this.currentFund].newAmount ? this.state.funds[this.currentFund].newAmount : this.state.funds[this.currentFund].amount)}</h5>
                        </Col>

                        <Col xs={6} md={6} sm={6}>

                            <h5> New Remaining balance :
                                {MoneyFormater.format(this.state.newRemainingBalance!=undefined ? this.state.newRemainingBalance : this.state.remainingAccountBalance)}</h5>
                        </Col>

                    </Row>
                    <Row>
                        <Col xs={12}>
                            <Button onClick={this.executeTransfer} bsStyle="primary">Confirm Transfer</Button>
                        </Col>
                    </Row>
                </Panel>
            </Row>
        );
    }
}

export default SaveToBank;